<template>
  <div class='panel__wrapper'>
    <div class='panel__top'>
      <h2 class='panel__title'>{{ $t(' ')}}</h2>
      <div v-html='cross'
           class='cross'
           @click='hidePanel("isPriceOpened")'
      ></div>
    </div>

    <div class='panel__content'>
      <div class='label-wrapper'>
        <span class='input__title'>{{$t('')}}</span>
        <v-select :placeholder='$t(" ")'
                  :options='serviceGroups'
                  label='name'
                  v-model='selectedGroup'>
          <template slot="no-options">{{ $t(' ') }}</template>
        </v-select>
      </div>

      <div class='label-wrapper'>
        <span class='input__title'>{{ $t('') }}</span>
        <v-select :placeholder='$t(" ")'
                  :options='filteredServices'
                  label='name'
                  v-model='selectedService'
                  @change="selectedService = ''">
          <template #no-options="{ search, searching, loading }">
            <span>$t(' ')</span>
          </template>
          <template #option="{ name, price }">
            <div class='price__option-text'>
              <span>{{ name }}</span>
              <span>{{ price }}</span>
            </div>
          </template>
        </v-select>
      </div>


      <base-btn icon='plus'
                :text='$t(" ")'
                :title='!selectedService ? $t(" "): ""'
                :disabled='!selectedService'
                @click.native='addServiceToTotalList("service")'
      ></base-btn>

      <div>
        <span class='input__title'>{{ $t("") }}</span>
        <v-select :placeholder='$t(" ")'
                  :options='warehouses'
                  label='name'
                  v-model='selectedWarehouse'
                  >
          <template #no-options >
            $t(" ")
          </template>
          <template #option="{ name, sale_price }">
            <div class='price__option-text'>
              <span>{{ name }}</span>
              <span>{{ sale_price }}</span>
            </div>
          </template>
        </v-select>
      </div>

      <base-btn icon='plus'
                :text='$t(" ")'
                :title='!selectedWarehouse ? " ": ""'
                :disabled='!selectedWarehouse'
                @click.native='addServiceToTotalList("warehouse")'
      ></base-btn>

      <service-material-table
          :table='totalList'
          :specCol='false'
          :dateCol='false'
          :greyThead='true'
          @delete-table-item='deleteItem($event)'
      ></service-material-table>

      <base-btn text=''
                v-if='isPanelDataEmpty'
                @click.native='resetPanelData'
      ></base-btn>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { clock, cross } from '@/helpers/icons.js';

import { serviceMixin } from '@/mixins/ServiceMaterialsMixin.js';

import ServicesMaterialTable from "@/components/Tables/ServiceMaterialTable.vue";
import axios from "axios";
export default {
  name: 'PricePanel',
  mixins: [serviceMixin],
  components: {
    'service-material-table': ServicesMaterialTable,
  },
  data() {
    return {
      clock,
      cross,
      theadCalcPrice: ['   ', ` <br>. (&#8381;)`, '-', `<br>(&#8381;)`],
      printPageTemplate: '',
    };
  },

  computed: {
    ...mapState({
      isPriceOpened: (state) => state.panels.isPriceOpened,
    }),

    isPanelDataEmpty() {
      return (
          this.totalList.length ||
          this.selectedWarehouse ||
          this.selectedService ||
          this.selectedGroup
      );
    },
  },
  methods: {
    hidePanel(panel) {
      this.$store.commit('panels/closePanel', panel);
    },
    getServices(search, loading) {
      clearTimeout(this.timer);
      if (search.length) {
        loading(true);
        this.timer = setTimeout(() => {
          return priceApi
              .getServices({ query: search })
              .then((res) => {
                clearTimeout(this.timer);
                this.services = res.data;
                loading(false);
              })
              .catch(loading(false));
        }, 1500);
      }
    },

    resetPanelData() {
      this.totalList = [];
      this.selectedWarehouse = '';
      this.selectedService = '';
      this.selectedGroup = '';
    },
  },
}
</script>

